.App {
  text-align: center;
  margin: 5% 0;
}

@font-face {
  font-family: "ColfaxAI";
  src: url(https://cdn.openai.com/API/fonts/ColfaxAIRegular.woff2)
      format("woff2"),
    url(https://cdn.openai.com/API/fonts/ColfaxAIRegular.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "ColfaxAI";
  src: url(https://cdn.openai.com/API/fonts/ColfaxAIBold.woff2) format("woff2"),
    url(https://cdn.openai.com/API/fonts/ColfaxAIBold.woff) format("woff");
  font-weight: bold;
  font-style: normal;
}

.icon {
  width: 90px;
}
h3 {
  font-size: 32px;
  line-height: 40px;
  font-weight: bold;
  color: #202123;
  margin: 16px 0 40px;
}
form {
  display: flex;
  flex-direction: column;
  width: 25%;
  margin: 0 auto;
}
input {
  font-size: 16px;
  line-height: 24px;
  color: #353740;
  font-family: "ColfaxAI", Helvetica, sans-serif;
}
input[type="text"] {
  padding: 12px 16px;
  border: 1px solid #10a37f;
  border-radius: 4px;
  margin-bottom: 24px;
}
::placeholder {
  color: #8e8ea0;
  opacity: 1;
}
input[type="submit"] {
  padding: 12px 0;
  color: #fff;
  background-color: #5783cf;
  border: none;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
}
.result {
  width: 50%;
  font-weight: bold;
  margin: 3em auto;

}
.result h4 {
  font-size: 1.2em;
  line-height: 1.2em;
  letter-spacing: 1px;
  background-color: #fafafa;
  border: #e4e4e4 1px solid;
  border-radius: 10px;
  padding: 2em;
  color: #333
}
@media only screen and (max-width: 1300px) {
  form {
    width: 50%;
  }
}
@media only screen and (max-width: 700px) {
  form {
    width: 75%;
  }
}
